import React from 'react';

//matrial react
import {Button, TextField} from '@material-ui/core';

//imported images
import google from '../assets/imgs/Group_1811.png';
import facebook from '../assets/imgs/Path_1146.png';
import instgram from '../assets/imgs/Union_21.png';

class Home extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      email: '',
      pass: ''
    };
  }
  handleEmailChange = (e) => this.setState({email: e.target.value, pass: this.state.pass})

  handlePassChange = (e) => this.setState({pass: e.target.value, email: this.state.email})

  login() {
    console.log(this.state.pass, this.state.email);
    return this
      .props
      .handleLogin(this.state.email, this.state.pass)
  }

  render() {
    return <div>

      <div className="body-container">
        <div className="container">
          <div className="col-lg-6 offset-lg-3">
            <div className="login-card">
              <div className="login-card-img">
                <h3>LOGIN</h3>
              </div>
              <div className="social-login">
                <p>CONNTACT BY SOCIAL MEDIA</p>
                <div className="row">
                  <div className="col-4 col-padding">
                    <div className="social-login-link google-login">
                      <img src={google}/>
                    </div>
                  </div>
                  <div className="col-4 col-padding">
                    <div className="social-login-link facebook-login">
                      <img src={facebook}/>
                    </div>
                  </div>
                  <div className="col-4 col-padding">
                    <div className="social-login-link instagram-login">
                      <img src={instgram}/>
                    </div>
                  </div>
                </div>
              </div>
              <form
                className="login-form"
                noValidate
                autoComplete="off"
                onSubmit={this.login}>
                <TextField
                  value={this.state.email}
                  onChange={(e) => this.handleEmailChange(e)}
                  className="col-12"
                  type="text"
                  id="standard-basic"
                  label="EMAIL"/>

                <TextField
                  value={this.state.pass}
                  onChange={(e) => this.handlePassChange(e)}
                  className="col-12"
                  type="password"
                  id="inputPassword"
                  label="PASSWORD"/>

                <a className="forget-pass" href="#">FORGET PASSWORD?</a>

                <Button
                  className="col-12 submit"
                  onClick={() => this.login()}
                  variant="contained">
                  Login
                </Button>
                <p className="sign-up-link">DON'T HAVE AN ACCOUNT?
                  <a href="#">SIGN UP</a>
                  BY EMAIL!</p>
              </form>

            </div>
          </div>
        </div>
      </div>

    </div>
  }
}

export default Home;