import React from 'react';

//matrial react
import {Button, TextField} from '@material-ui/core';

//imported images
import footerLogo from '../assets/imgs/logo-footer.png';
import tv from '../assets/imgs/tv_1_.png';
import mark from '../assets/imgs/footer-mark.jpg';

//fontawsome icons
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faPhoneAlt, faPlay} from '@fortawesome/free-solid-svg-icons'
import {
  faInstagram,
  faFacebookF,
  faTwitter,
  faPinterestP,
  faVimeoV,
  faApple,
  faAndroid,
  faYoutube
} from '@fortawesome/free-brands-svg-icons';
const Instagram = <FontAwesomeIcon icon={faInstagram}/>
const Phone = <FontAwesomeIcon icon={faPhoneAlt}/>
const Pin = <FontAwesomeIcon icon={faPinterestP}/>
const Vimeo = <FontAwesomeIcon icon={faVimeoV}/>
const Twitter = <FontAwesomeIcon icon={faTwitter}/>
const Play = <FontAwesomeIcon icon={faPlay}/>
const Apple = <FontAwesomeIcon icon={faApple}/>
const Android = <FontAwesomeIcon icon={faAndroid}/>
const Facebook = <FontAwesomeIcon icon={faFacebookF}/>
const Youtube = <FontAwesomeIcon icon={faYoutube}/>

class Footer extends React.Component {
  render() {
    return <div>
      <div className="subscribe">
        <div className="container">

          <div className="offset-lg-1 col-lg-10 col-12 ">
            <div className="row">
              <h4 className="col-md-5 col-12">Get our daily newslatter.Sign up</h4>
              <TextField
                className="col-md-5 col-12"
                type="text"
                id="standard-basic"
                label="your email"/>
              <div className="col-md-2 col-12">
                <Button className="go-button">
                  Go!
                </Button>
              </div>
            </div>
          </div>

        </div>
      </div>
      <footer>
        <div className="container">
          <div className="top-footer">
            <div className="row">
              <div className="col-lg-6 col-12 footer-right-side">
                <img className="footer-logo" src={footerLogo} alt=""/>
                <p className="footer-pref">
                  Fustany.com is a fashion$lifestyle portal by Arab women for<br/>
                  Arab women to inspire them to live a kife full of creativity.
                </p>
              </div>
              <div className="col-lg-6 col-12 footer-left-side">
                <div className="social-media-list">
                  <ul>
                    <img src={mark} alt=""/>
                    <li className="footer-tv">
                      <a href="#"><img src={tv} alt=""/></a>
                    </li>
                    <li>
                      <a href="#">{Instagram}</a>
                    </li>
                    <li>
                      <a href="#">{Phone}</a>
                    </li>
                    <li>
                      <a href="#">{Pin}</a>
                    </li>
                    <li>
                      <a href="#">{Vimeo}</a>
                    </li>
                    <li>
                      <a href="#">{Facebook}</a>
                    </li>
                    <li>
                      <a href="#">{Twitter}</a>
                    </li>
                    <li>
                      <a href="#">{Play}</a>
                    </li>
                    <li>
                      <a href="#">{Android}</a>
                    </li>
                    <li>
                      <a href="#">{Apple}</a>
                    </li>
                    <li>
                      <a href="#">{Youtube}</a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div className="bottom-footer">
            <div className="row">
              <div className="col-lg-8 col-12">
                <ul className="footer-nav">
                  <li>
                    <a href="">Home</a>
                  </li>
                  <li>
                    <a href="">what's Fustany</a>
                  </li>
                  <li>
                    <a href="">Advirtise</a>
                  </li>
                  <li>
                    <a href="">Contact Us</a>
                  </li>
                  <li>
                    <a href="">Careers</a>
                  </li>
                  <li>
                    <a href="">Privacy Police</a>
                  </li>
                </ul>
              </div>
              <div className="col-lg-4 col-12 bottom-footer-left">
                <p>&copy; 2020 Fustany.com</p>
              </div>
            </div>
          </div>
        </div>
      </footer>
    </div>
  }
}

export default Footer;