import React from 'react';
import * as ls from "local-storage";
import ReactQuill from 'react-quill'; // ES6

const LOCAL_STORAGE_NAME = "fustany-storage";

class Editor extends React.Component {
  constructor() {
    super();
    this.state = {
      text: ''
    };
  }

  componentDidMount() {
    //get localstrorage  value
    const savedLocalStorage = ls.get(LOCAL_STORAGE_NAME);
    this.setState({text: savedLocalStorage});
  }

  handleChange(text) {
    //set localstrorage key and value
    ls.set(LOCAL_STORAGE_NAME, text);
    this.setState({text: text});
  }

  render() {
    return <div>
      <div className="body-container">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h2 className="editor-h">Fustany Posts Editor</h2>
            </div>
          </div>
          <div className="row">
            <div className="form-group offset-md-2 col-md-8">
              <ReactQuill
                value={this.state.text}
                type="text"
                onChange=
                {v => this.handleChange(v)}/>
            </div>
          </div>
        </div>
      </div>
    </div>
  }
}

export default Editor;