import React from 'react';
import Login from './components/login';
import Editor from './components/editor';
import Header from './layout/header';
import Footer from './layout/footer';
import './App.css';
import axios from 'axios';
import Cookies from 'js-cookie'
class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      error: '',
      isOAuth: false
    }
    /*this.handleLogin = this
      .handleLogin
      .bind(this);*/
  }

  componentDidMount() {
    let cookie = Cookies.get('fustany-token');
    if (cookie) 
      this.setState({isOAuth: true});

    }
  
  handleLogin(email, pass) {
    const post = {
      method: 'POST',
      headers: { 
          'content-type': 'application/json', 
          'X-App-Token': 'Fustany'
      },
      data: {
          admin: {
              role: 'Admin',
              password: pass,
              email: email
          }
      },
      url: 'http://dev.fustany.com//en/v2/admin/sessions.json?expires_in=1%60',
    };
    return axios(post).then((response) => {
      this.setState({isOAuth: true});
      Cookies.set('fustany-token', response.data.token, {expires: 1});
      return response;
    })
    .catch((error) => {
      this.setState({error: error.error});

    });
}

   /* return axios(options).then((response) => {
      this.setState({isOAuth: true});
      Cookies.set('fustany-token', response.data.token, {expires: 1});
      return response;
    }).catch((error) => {
      this.setState({error: error.error});

    });
  }*/

  render() {
    return <div>
      <Header/> {(this.state.isOAuth === true) && <Editor/>
}
      {(!this.state.isOAuth) && <Login handleLogin= {(email, pass) => this.handleLogin(email, pass)} error={this.state.error}/>
}
      <Footer/>
    </div>
  }
}

export default App;