import React from 'react';

//react-bootstrap
import {Nav, Navbar} from 'react-bootstrap';

//imported images
import logo from '../assets/imgs/logo.png';
import motherhood from '../assets/imgs/motherhood.png';
import leftNav from '../assets/imgs/left-nav.jpg';
import tv from '../assets/imgs/tv_1__1.png';

//fontawsome icons
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faBars, faSearch} from '@fortawesome/free-solid-svg-icons'

const Bars = <FontAwesomeIcon icon={faBars}/>
const Search = <FontAwesomeIcon icon={faSearch}/>

class Header extends React.Component {
  render() {
    return <div>
      <header>
        <div className="top-nav">
          <div className="container">
            <div className="row">

              <div className="col-lg-4 top-nav-left">
                <span>{Bars} EXPLORE</span>
                <a className="who" href="#">WHO IS FUSTANY</a>
              </div>

              <div className="col-lg-4 logo">
                <span><img src={logo} alt=""/></span>
              </div>

              <div className="col-lg-4 top-nav-right">
                <ul>
                  <li>
                    <a href="#">WRITE WITH US</a>
                  </li>
                  <li>
                    <a href="#">SUBSCRIBE</a>
                  </li>
                  <li>
                    <a href="#"><img className="login-icon" src={motherhood} alt=""/>LOGIN</a>
                  </li>
                </ul>
              </div>

            </div>
          </div>
        </div>
        <div className="main-nav">
          <div className="container">
            <div className="row">
              <div className="col-lg-3 ask d-none d-md-none d-lg-block">
                <img src={leftNav} alt=""/>
              </div>
              <Navbar className="col-lg-6" expand="lg">

                <Navbar.Toggle aria-controls="basic-navbar-nav"/>
                <Navbar.Collapse id="basic-navbar-nav">
                  <Nav>
                    <Nav.Link href="#home">HOME</Nav.Link>
                    <Nav.Link href="#link">RAMADAN 19</Nav.Link>
                    <Nav.Link href="#home">FASHION</Nav.Link>
                    <Nav.Link href="#home">BEAUTY</Nav.Link>
                    <Nav.Link href="#home">LIFESTYLE</Nav.Link>
                    <Nav.Link href="#home">RECIPES</Nav.Link>
                  </Nav>
                  <div className="col-lg-3 d-lg-none d-md-block ">
                    <ul className="right-navbar row">
                      <li className="search col">
                        <a href="#">{Search}</a>
                      </li>
                      <li className="col">
                        <a href="#"><img src={tv} alt=""/></a>
                      </li>
                      <li className="col">
                        <a href="#">عربي</a>
                      </li>
                    </ul>
                  </div>
                </Navbar.Collapse>
              </Navbar>
              <div className="col-lg-3 d-none d-md-none d-lg-block ">
                <ul className="right-navbar ">
                  <li>
                    <a href="#">عربي</a>
                  </li>
                  <li>
                    <a href="#"><img src={tv} alt=""/></a>
                  </li>
                  <li className="search">
                    <a href="#">{Search}</a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </header>
    </div>
  }
}

export default Header;